import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import routeConstant from 'routes/route/routeConstant';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getCandidates, deleteCandidate } from 'services/studentCandidate';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchCandidate from './FormSearchCandidate';

class ListCandidateStudentPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteCandidate({ id });
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'SBD',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H tn',
        dataIndex: 'id',
        key: 'code',
        render: (text, record) => `${record.firstname} ${record.lastname}`,
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'M trng',
        dataIndex: 'schoolId',
        key: 'schoolId',
      },
      {
        title: 'KV',
        dataIndex: 'areaId',
        key: 'areaId',
      },
      {
        title: 'Thng tr',
        dataIndex: 'permanentAddressLabel',
        key: 'permanentAddressLabel',
      },
      {
        title: 'Nm TN',
        dataIndex: 'graduatedYear',
        key: 'graduatedYear',
      },
      {
        title: 'H T',
        dataIndex: 'trainType',
        key: 'trainType',
      },
      {
        title: 'M ngnh',
        dataIndex: 'studyField',
        key: 'studyField',
      },
      {
        title: 'Khi',
        dataIndex: 'testGroup',
        key: 'testGroup',
      },
      {
        title: 'Mn 1',
        dataIndex: 'gradeM1',
        key: 'gradeM1',
      },
      {
        title: 'Mn 2',
        dataIndex: 'gradeM2',
        key: 'gradeM2',
      },
      {
        title: 'Mn 3',
        dataIndex: 'gradeM3',
        key: 'gradeM3',
      },
      {
        title: 'TC',
        dataIndex: 'totalGrade',
        key: 'totalGrade',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.TUYEN_SINH_NHAP_HOC__UNG_VIEN__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="">
        <CartHeadAction>
          <Link to={routeConstant.TUYEN_SINH_NHAP_HOC__UNG_VIEN__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <FormSearchCandidate />
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getCandidates}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListCandidateStudentPage {...props} />
  </Page>
));



